ojects__uppersection .uppersection__text-wrapper .uppersection__subtitle{font-size:10px;line-height:12px;text-align:center}.projects__content{gap:11px}.projects__content .content__row{gap:21px}.projects__content .content__row__image{max-width:149px;max-height:54px}.projects .content__image{max-width:128px;max-height:128px}}